    bottom: -5px;
    left: 1px;
    border-right-color: #303133;
    border-left-width: 0;
}

.el-tooltip__popper[x-placement^="left"] {
    margin-right: 12px;
}

.el-tooltip__popper[x-placement^="left"] .popper__arrow {
