    padding: 5px 0;
    margin: 0;
    list-style: none;
    min-width: 100px;
}

.el-table-filter__list-item {
    line-height: 36px;
    padding: 0 10px;
    cursor: pointer;
    font-size: 14px;
}

.el-table-filter__list-item:hover {
    background-color: #e8f2ff;
    color: #4999fd;
}

.el-table-filter__list-item.is-active {
