}

.el-table-filter__wrap {
    max-height: 280px;
}

.el-table-filter__checkbox-group {
    padding: 10px;
}

.el-table-filter__checkbox-group label.el-checkbox {
    display: block;
    margin-right: 5px;
    margin-bottom: 8px;
    margin-left: 5px;
}

.el-table-filter__checkbox-group .el-checkbox:last-child {
    margin-bottom: 0;
}

