.el-notification {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 330px;
    padding: 14px 26px 14px 13px;
    border-radius: 8px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    border: 1px solid #ebeef5;
    position: fixed;
    background-color: #fff;
    -webkit-box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    -webkit-transition: opacity .3s, left .3s, right .3s, top 0.4s, bottom .3s, -webkit-transform .3s;
    transition: opacity .3s, left .3s, right .3s, top 0.4s, bottom .3s, -webkit-transform .3s;
    transition: opacity .3s, transform .3s, left .3s, right .3s, top 0.4s, bottom .3s;
    transition: opacity .3s, transform .3s, left .3s, right .3s, top 0.4s, bottom .3s, -webkit-transform .3s;
    overflow: hidden;
}

.el-notification.right {
    right: 16px;
}

.el-notification.left {
    left: 16px;
}

.el-notification__group {
    margin-left: 13px;
}

.el-notification__title {
    font-weight: bold;
    font-size: 16px;
    color: #303133;
    margin: 0;
}

.el-notification__content {
    font-size: 14px;
    line-height: 21px;
    margin: 6px 0 0 0;
    color: #606266;
    text-align: justify;
}

.el-notification__content p {
    margin: 0;
}

.el-notification__icon {
    height: 24px;
    width: 24px;
    font-size: 24px;
}

.el-notification__closeBtn {
    position: absolute;
    top: 18px;
    right: 15px;
    cursor: pointer;
    color: #909399;
    font-size: 16px;
}

.el-notification__closeBtn:hover {
    color: #606266;
}

.el-notification .el-icon-success {
    color: #67c23a;
}

.el-notification .el-icon-error {
    color: #f56c6c;
}

.el-notification .el-icon-info {
    color: #909399;
}

.el-notification .el-icon-warning {
    color: #e6a23c;
}

.el-notification-fade-enter.right {
    right: 0;
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
}

.el-notification-fade-enter.left {
    left: 0;
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
}

.el-notification-fade-leave-active {
    opacity: 0;
}

