.el-color-dropdown {
    width: 300px;
}

.el-color-dropdown__main-wrapper {
    margin-bottom: 6px;
}

.el-color-dropdown__main-wrapper::after {
    content: "";
    display: table;
    clear: both;
}

.el-color-dropdown__btns {
    margin-top: 6px;
    text-align: right;
}

.el-color-dropdown__value {
    float: left;
    line-height: 26px;
    font-size: 12px;
    color: #000;
    width: 160px;
}

.el-color-dropdown__btn {
    border: 1px solid #dcdcdc;
    color: #333;
    line-height: 24px;
    border-radius: 2px;
    padding: 0 20px;
    cursor: pointer;
    background-color: transparent;
    outline: none;
