/*登录之后顶部*/

.lyf_main_all {}

.lyf_main_logo {
    height: 150px;
    width: 100%;
}

.lyf_head_logo {
    height: 150px;
    width: auto;
    min-height: 100%;
    min-width: 100%;
    position: relative;
    z-index: 1;
    left: 0;
    right: 0;
    overflow: hidden;
}

/* /登录之后顶部*/

.lyf_biaoduan_search {
    background-color: #FAFAFA;
    height: 55px;
    line-height: 55px;
    border: 1px solid #D6D7DC;
    padding: 0 20px;
    /*margin-bottom: 20px;*/
}

.lyf_biaoduan_search_input {
    margin-top: -2px;
    line-height: 53px;
    /*border: 1px solid red;*/
}

/*头部搜索*/

.lyf_head_search {
    max-width: 1200px;
    height: 192px;
    line-height: 192px;
    display: table;
    margin: 0 auto;
    overflow: hidden;
}

.el-input-group__append button.el-button {
    background-color: #308AFE;
    color: #fff;
    border: 1px solid #308AFE;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    border-top: 0;
    border-bottom: 0;
}

.lyf_head_search_button {
    width: 90px;
    height: 40px;
}

.lyf_head_input {
    width: 320px;
    height: 40px;
}

.lyf_head_input .el-input-group--append .el-input__inner {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border: 1px solid #1C7FFD;
}

.lyf_head_title {
    font-size: 20px;
    padding-left: 20px;
}

/*网站名及当前日期*/

.lyf_head_elrow {
    padding: 0 50px;
    background-color: rgba(255, 255, 255, 0.3);
    ;
    height: 70px;
    line-height: 70px;
}

.lyf_head_elrow_title {
    font-size: 30px;
    float: left;
}

.lyf_head_elrow_date {
    font-size: 20px;
    color: #fff;
    float: right;
}

