/*选项(一级路由)*/

.lyf_head_nav {
    overflow: hidden;
    text-align: center;
    background: #308AFE;
    height: 60px;
}

.lyf_head_nav>ul {
    width: 1200px;
    display: table;
    margin: 0 auto;
}

.lyf_head_nav>li {
    /*width:148px;*/
    width: 130px;
    float: left;
    height: 60px;
}

.lyf_head_nav>li>a {
    text-decoration: none;
    text-align: center;
    height: 35px;
    line-height: 60px;
    font-size: 16px;
    color: #fff;
}

.lyf_head_nav_length>a {
    width: 150px;
}

.lyf_head_xingweijilu {
    background-color: #3479D0;
    border-radius: 20px;
    padding: 6px 12px;
    color: #fff;
    text-decoration: none;
    text-align: center;
    height: 35px;
    line-height: 50px;
    font-size: 16px;
    color: #fff;
}

.lyf_head_xingweijilu_notactive {
    text-decoration: none;
    text-align: center;
    height: 35px;
    line-height: 50px;
    font-size: 16px;
    color: #fff;
}

.lyf_head_nav>li>a:hover {
    background-color: #3479D0;
    border-radius: 20px;
    padding: 6px 12px;
}

.lyf_head_nav_select {
    text-align: center;
    display: block;
    height: 33px;
    line-height: 33px;
